import hotkeys from 'hotkeys-js';
import { useEffect } from 'react';

function useHotkeys(keys, callback) {
  useEffect(function () {
    hotkeys(keys, function (event, handler) {
      return callback(event, handler);
    });
    return function () {
      return hotkeys.unbind(keys);
    };
  }, [callback]);
}

export { useHotkeys };